<template>
<div>
  <cms-breadcrumb to="/role/list">
    <li>{{$route.name}}</li>
  </cms-breadcrumb>
<div class="cms-list-layout">    
   <cms-prompt>
      <li>显示账号组内的用户信息列表,可移除账号组内用户</li>
    </cms-prompt>
    <section class="cms-batch">
      <cms-button v-prem="'/admin/save'" icon="add" type="success" size="small" @click="$routerLink('/admin/save','save')">新增</cms-button>
      <cms-button icon="error" type="error" size="small" @click="removeBatch(ids)">批量删除</cms-button>
    </section>
    <el-table :data="tableData" style="width: 100%" stripe  @selection-change='getChangeIds' @row-click='checkRows' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="username" label="店铺账户" width="110"></el-table-column>
      <el-table-column prop="mobile" label="手机" min-width="130"></el-table-column>
      <el-table-column prop="email" label="邮箱" min-width="160"></el-table-column>
      <el-table-column prop="" label="是否启用" align="center">
        <template slot-scope="scope">
          <span>{{scope.row.enabled?'是':'否'}}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" width='255' fixed="right" align="center">
        <template slot-scope="scope">
          <cms-button icon='error' size='small' type='info' @click.stop="removeUser(scope.row.id)">移除</cms-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</div>
</template>
<script>
import list from '@/mixins/list'

import { getUserRoleList, removeUserRole } from './../../../../api/admin/role.js'
export default {
  mixins: [list],
  data() {
    return {
      tableData: [
        {
          'email': '',
          'enabled': '',
          'id': [],
          'mobile': '',
          'username': ''
        }
      ]
    }
  },
  methods: {
    getUserList() {
      getUserRoleList({ id: this.$route.query.id })
        .then(res => {
          if (res.code === 200) {
            this.tableData = res.data.storeUsers
          }
        })
    },
    removeUser(userId) {
      this.$confirm('确定移除该成员?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        removeUserRole({ id: this.$route.query.id, userId: [userId] })
          .then(res => {
            this.$successMessage(res.message)
            this.getUserList()
          })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    getChangeIds(ids) {
      this.tableData.ids = ids
    },
    removeBatch() {
      var that = this
      if (that.tableData.ids.length > 0) {
        var selectedIds = []
        that.tableData.ids.forEach(v => {
          selectedIds.push(v.id)
        })
        this.$confirm('确定要移除选定的成员?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          removeUserRole({ id: this.$route.query.id, userId: selectedIds })
            .then(res => {
              if (res.code === 200) {
                this.$errorMessage(res.message)
                this.getUserList()
              }
            })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
      } else {
        this.$errorMessage('请先选中之后在删除')
      }
    }
  },
  created() {
    this.getUserList()
  }
}
</script>
<style>

</style>




